
.woocommerce {
    .main-content__product {
        display: flex;
        flex-wrap: wrap;
        .image-wrapper-product {
            max-width: 50%;
            flex: 0 0 50%;
            position: relative;
            .product-gallery {
                position: relative;
                img {
                    -o-object-fit: cover;
                    object-fit: cover;
                    height: 100%;
                    width: 100%;
                    position: absolute;
                    top: 0;
                    -o-object-position: center;
                    object-position: center;
                }
            }
            .image-thumbs {
                width: 100%;
            }
            .swiper-wrapper.wrapper-slider-vertical {
                display: flex;
                flex-direction: column;
                width: 100%;
                .swiper-slide {
                    width: 100% !important;
                }
                img {
                    width: 100%;
                    max-width: 100%;
                    margin-bottom: 20px;
                    cursor: pointer;
                }
            }
        }
        .product-summary {
            max-width: 50%;
            flex: 0 0 50%;
            padding: 100px 150px;
            .product-name {
                margin: 5px 0;
                h1 {
                    font-weight: 800;
                    font-size: 3rem;
                }
            }
            .price {
                color: $black;
                text-decoration: none;
                margin-bottom: 40px;
                del {
                    font-size: 1.5rem;
                }
                .woocommerce-Price-currencySymbol {
                    margin-right: 2px;
                }
                ins {
                    text-decoration: none;
                    font-weight: 800;
                    font-size: 1.5rem;
                }
            }
            .description-excerpt-product {
                margin: 40px 0;
                p {
                    margin-bottom: 0;
                }
            }
            .composizione {
                span {
                    color: #2b2b2b;
                    font-weight: 300;
                    margin-right: 10px;
                }
            }
            .consegna-entro {
                color: #666;
                margin-bottom: 30px;
                span {
                    font-weight: 600;
                    color: #2b2b2b;
                }
            }
            .icon-lavaggi {
                margin-left: -6px;
                img {
                    width: 30px;
                }
            }
            .variations {
                .label-attribute {
                    display: none;
                }
                .reset_variations {
                    display: none !important;
                }
                .variable-items-wrapper {
                    .variable-item {
                        border-radius: 0px !important;
                        font-weight: 600;
                        font-size: 1.2rem !important;
                        box-shadow: none;
                        border: 1px solid $black;
                        width: 30px !important;
                        &.selected {
                            background: $black;
                            color: $white !important;
                        }
                        span {
                            font-size: 1.2rem !important;
                        }   
                    }
                }
                .select2-container  {
                    width: 100% !important;
                    .select2-selection--single {
                        border: 0;
                        border: 1px solid #dedede;
                        border-radius: 0;
                        height: 50px;
                        .select2-selection__rendered {
                            line-height: 50px;
                            padding: 0 25px
                        }
                        .select2-selection__arrow {
                            margin-top: 10px;
                        }
                    }
                }
                .select2-dropdown {
                    border: 1px solid #dedede;
                    border-top: 0;
                    border-radius: 0;
                    .select2-results__option {
                        padding: 15px 25px;
                        border-bottom: 1px solid #dedede;
                        
                    }
                    .select2-results__option--highlighted[aria-selected], .select2-container--default .select2-results__option--highlighted[data-selected] {
                        background-color: $primary;
                    }
                    .select2-container--default .select2-results__option[aria-selected=true] {
                        background-color: $primary;
                    }
                }
            }
            .product-btn-quantity {
                display: flex;
                flex-wrap: wrap;
                margin-top: 20px;
                .quantity_select {
                    float: inherit !important;
                    max-width: 15%;
                    flex: 0 0 15%;
                    height: 50px;
                    display: flex;
                    margin: 0 !important;
                }
                .select2-container {
                    width: 100% !important;
                }
                #select2-quantity-p1-container {
                    height: 50px !important;
                    display: flex;
                    align-items: center;
                    font-weight: 600;
                    color: $black;
                }
                .select2-dropdown {
                    border: 1px solid $black;
                    border-bottom: 0 !important;
                    border-radius: 4px;
                    border-radius: 0;
                }
                .select2-selection__rendered {
                    height: 50px;
                    display: flex;
                    align-items: center;
                    font-weight: 600;
                    color: $black;
                }
                span.select2.select2-container.select2-container--default.select2-container--above {
                    width: 100% !important
                }
                .select2-selection.select2-selection--single {
                    height: 50px;
                    border: 1px solid #000;
                    border-radius: 0;
                    border-right: 0;
                }
                span#select2-quantity-uf-container {
                    height: 50px;
                    display: flex;
                    align-items: center;
                    font-size: 1.5rem;
                    font-weight: 400;
                    color: #000;
                }
                .select2-container--default .select2-selection--single .select2-selection__arrow b {
                    top: 25px;
                    border-color: #000 transparent transparent transparent;
                    left: 15%;
                }
                .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b{
                    border-color: transparent transparent $black transparent;
                }
                .single_add_to_cart_button{
                    max-width: 85%;
                    flex: 0 0 85%;
                    font-size: 1.5rem;
                    line-height: 120%;
                    letter-spacing: -1px;
                    background-color: $black;
                    border-radius: 0;
                    color: $white;
                    height: 50px;
                    line-height: 50px;
                    width: 100%;
                    text-align: center;
                    text-decoration: none;
                    text-transform: uppercase;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: ease-in-out all 0.5s;
                    &.disabled {
                        opacity: 1;
                    }
                    &:hover {
                        background-color: #d9d9d9 !important;
    
                        color: $black;
                    }
                }
            }
        }
    }
}
.nuovi-arrivi {
    margin-top: 90px;
}
.ProductDescription__links {
    margin-bottom: 20px;
    a {
        font-size: 1.3rem;
        margin-right: 25px;
    }
}
.free-shipping {
    padding: 40px 0 90px;
    font-size: 1.2rem;
    letter-spacing: 2px;
}
.product-bottom {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}
.product-bottom__item{
    &.text-grey  {
        color: #d8d8d8;
    }
}
.woocommerce-tabs.wc-tabs-wrapper {
    display: none;
}
.more-details {
    padding: 120px 0;
    .wrapper-small {
        max-width: 600px;
        margin: 0 auto;
    }
    .title-central {
        margin-bottom: 25px;
    }
}