/******************* Header ***********************/

.site-header {
    background: #fff !important;
    padding: 10px 0;
    width: 100%;
    z-index: 99;
    position: relative;
    -webkit-transition: ease-in-out all .5s;
    transition: ease-in-out all .5s;
    &.scroll {
        -webkit-transition: ease-in-out all .5s;
        transition: ease-in-out all .5s;
        position: fixed;
        transform: translateY(0px);
    }
    .site-header-wrapper {
        .logo {
            img {
                width: 100px;
            }
            svg {
                width: 180px;
                height: auto;
            }
        }
        .site-nav {
            ul {
                display: flex;
                align-items: center;
                padding: 0;
                margin-bottom: 0;
                li {
                    font-weight: 600;
                    padding: 0 25px;
                    line-height: .8;
                    text-transform: uppercase;
                    font-size: 1.4rem;
                    position: relative;
                    a {
                        color: $main-color;
                        &:hover {
                            color: $sand;
                        }
                    }
                    &:last-child {
                        border-right: 0;
                    }
                    &.current-menu-item {
                        position: relative;
                        a {
                            color: $primary;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            bottom: -10px;
                            width: 90%;
                            height: 1px;
                            background: $primary;
                            left: 5%;
                        }
                    }
                    &.current_page_parent {
                        a {
                            color: $primary;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            bottom: -10px;
                            width: 90%;
                            height: 1px;
                            background: $primary;
                            left: 5%;
                        } 
                    }
                    .sub-menu {
                        position: absolute;
                        z-index: 99;
                        flex-direction: column;
                        align-items: flex-start;
                        width: 150px;
                        top: 30px;
                        left: 15px;
                        background-color: #fff;
                        box-shadow: 0 0 3px rgba(0,0,0,0.2);
                        padding: 20px;
                        opacity: 0;
                        transition: ease 0.5s;
                        li {
                            padding: 0 !important;
                            border-right: 0;
                            margin-bottom: 20px;
                            text-transform: capitalize;
                            &:last-child {
                                margin-bottom: 0;
                            }
                            a {
                                color: $main-color;
                            }
                            &.current-menu-item {
                                position: relative;
                                a {
                                    color: $primary;
                                }
                                &:after {
                                    content: none;
                                }
                            }
                        }
                    }
                    &:hover {
                        .sub-menu {
                            opacity: 1;
                            li {

                            }
                        }
                    }
                }
            }
        }
        .shopping-cart{
            svg {
                width: 20px;
                height: 18px;
            }
        }
        .account {
            margin-right: 20px;
            a {
                line-height: .8;
                text-transform: uppercase;
                font-weight: 600;
                letter-spacing: 3px;
                font-size: 1.5rem;
                color: $main-color;
            }
        }
    }
    .social-nav {
        margin-right: 18px;
        ul {
            display: flex;
            li {
                margin-right: 10px;
                font-size: 2rem;
                a {
                    color: $main-color;
                    svg {
                        width: 20px;
                        height: 20px;
                        fill: $main-color;
                    }
                    &:hover {
                        color: $primary;
                        svg {
                            fill: $primary;
                        }
                    }
                }
            }
        }
    }
    .topbar-right {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            -webkit-transform: translate(-50%,-50%);
            transform: translate(0%,-50%);
            width: calc(100% - 100px);
            background-color: $main-color;
            height: 1px;
            right: 100px;
        }
    }
    .call-center__header {
        svg {
            width: 80px;
            height: auto;
            .st0{
                fill:none;
            }
            .st1{
                fill:#009944;
            }
            .st2{
                fill: #009944;
            }
        }
    }
    .hamburger-nav {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        .label-menu {
            font-size: 1.7rem;
            font-weight: 500;
            color: $main-color;
            margin-top: 4px;
        }
        #nav-icon3 {
            width: 30px;
            height: 20px;
            position: relative;
            -webkit-transform: rotate(0);
            transform: rotate(0);
            -webkit-transition: .5s ease-in-out;
            transition: .5s ease-in-out;
            cursor: pointer;
            span {
                display: block;
                position: absolute;
                height: 3px;
                width: 100%;
                background: $main-color;
                border-radius: 9px;
                opacity: 1;
                left: 0;
                -webkit-transform: rotate(0deg);
                -moz-transform: rotate(0deg);
                -o-transform: rotate(0deg);
                transform: rotate(0deg);
                -webkit-transition: .25s ease-in-out;
                -moz-transition: .25s ease-in-out;
                -o-transition: .25s ease-in-out;
                transition: .25s ease-in-out;
                &:nth-child(1) {
                    top: 0px;
                }
                &:nth-child(2) {
                    top: 8px;
                }
                &:nth-child(3) {
                    top: 8px;
                }
                &:nth-child(4) {
                    top: 16px;
                }
            }
            &.open {
                span {
                    &:nth-child(1) {
                        top: 10px;
                        width: 0%;
                        left: 50%;
                    }
                    &:nth-child(2) {
                        -webkit-transform: rotate(45deg);
                        -moz-transform: rotate(45deg);
                        -o-transform: rotate(45deg);
                        transform: rotate(45deg);
                    }
                    &:nth-child(3) {
                        -webkit-transform: rotate(-45deg);
                        -moz-transform: rotate(-45deg);
                        -o-transform: rotate(-45deg);
                        transform: rotate(-45deg);
                    }
                    &:nth-child(4) {
                        top: 12px;
                        width: 0%;
                        left: 50%;
                    }
                }
            }
        }
    }
    .backToCart {
        margin-top: 5px;
        font-size: 1.3rem;
        color: $black;
        font-weight: 300;
        svg {
            width: 4px!important;
            margin-right: 5px;
            top: -1px;
            position: relative;
        }  
    }  
    .secure-payment {
        text-transform: uppercase;
        font-weight: 600;
    }
    #find-product {
        overflow: hidden;
        display: inline-block;
        &.active {
            .close-search {
                transform: translateY(0);
                transition: ease all 0.5s;
                opacity: 1;
            }
        }
    }
    a#find-product svg {
        width: 10px;
        height: 10px;
    }
    .close-search {
        top: -1px;
        position: relative;
        transform: translateY(-50px);
        display: inline-block;
        opacity: 0;
        transition: ease all 0.3s;
    }
    .search-form {
        opacity: 0;
        position: absolute;
        z-index: 99;
        top: 0;
        z-index: 9999;
        width: calc(100% - 140px);
        background: #fff;
        height: 42px;
        display: flex;
        align-items: center;
        transition: ease-in-out all 0.5s;
        transform: translateY(-100%);
        svg {
            width: 20px;
            margin-right: 5px;
        }
        input {
            height: 25px;
            padding: 0 5px;
            border: 0;
            width: 100%;
        }
        &.active {
            display: flex !important;
            opacity: 1;
            transform: translateY(0);
        }
    }  
    &.site-header-checkout {
        background: #f5f5f5 !important;
    }
    .site-mini-cart {
        cursor: pointer;
        .cart-items-count {
            .header-cart-count {
                width: 20px;
                height: 20px;
                background: #000;
                text-align: center;
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }
}
.fullscreen-open {
    .site-header {
        .logo {
            svg {
                .st0, .st1 {
                    fill: #fff;
                }
            }
        }
        .social-nav {
            ul {
                li {
                    a {
                        color: $white;
                    }
                }
            }
        }
        .topbar-right {
            &:before {
                background-color: $white;
            }
        }    
        .call-center__header {
            svg {
                .st1 {
                    fill: $white;
                }
                .st2 {
                    fill: $white;
                }
            }
        }
        .social-nav {
            ul {
                li {
                    a {
                        color: $white;
                        svg {
                            width: 25px;
                            height: 25px;
                            fill: $main-color;
                        }
                        &:hover {
                            color: $primary;
                        }
                    }
                }
            }
        }
        .hamburger-nav {
            #nav-icon3 {
                span {
                    background-color: $white;
                }
            }
        }
    } 
}
.mobile-offcanvas {
    display: none;
    position: fixed;
    top: 60px;
    z-index: 9998;
    height: 100vh;
    background: $white;
    width: 100%;
    text-align: center;
    .nav-mobile {
        padding-top: 40px;
        ul {
            padding: 0;
            margin: 0;
            li {
                a {
                    font-size: 1.2rem;
                    color: $black;
                }
                &.parent-item {
                    text-shadow: 1px 0 0;
                    margin-bottom: 30px;
                    font-size: 1.4rem;
                }
                .nav-submenu {
                    margin-top: 10px;
                    margin-bottom: 30px;
                    li {
                        text-shadow: inherit;
                        text-shadow: none !important;
                        margin-bottom: 15px;
                    }
                }
            }
        }
    }
}
.fullscreen-nav {
    z-index: 9999;
    opacity: 0;
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    background-color: $primary;
    transition: all 0.7s ease-in-out;
    visibility: hidden;
    .mobile-menu__nav {
        margin-top: 100px;
        ul {
            li {
                font-size: 2rem;
                margin-bottom: 15px;
                text-transform: uppercase;
                &:last-child {
                    margin-bottom: 0;
                }
                a {
                    color: $white;
                }
                &.menu-item-has-children {
                    .sub-menu {
                        margin-left: 20px;
                    }
                    li {
                        text-transform: capitalize;
                    }
                    a {
                        position: relative;
                        display: block;
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
    &.open {
        opacity: 1;
        visibility: visible;
    }
    .fullscreen-nav__wrapper {
        height: 100%;
        display: flex;
        align-items: flex-end;
        padding: 30px;
    }
}