}
input {
    border-radius: 0;
    border: 1px solid $grey;
    padding: 15px 25px;
    &:focus {
        box-shadow: none !important;
        outline: none !important;
    }
}
.checkbox-privacy {
    .container-checkbox {
        display: block;
        position: relative;
        padding-left: 35px;
        margin-bottom: 12px;
        text-align: center;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        &:hover {
            input {
                &~ .checkmark {
                    background-color: $grey;
                }
            }
        }
        .checkmark {
            position: absolute;
            top: 50%;
            left: 0;
            height: 25px;
            width: 25px;
            background-color: $grey;
            font-size: 1.5rem;
            transform: translate(0, -50%);
            margin-top: 2px;
            &:after {
                content: "";
                position: absolute;
                display: none;
                left: 9px;
                top: 5px;
                width: 5px;
                height: 10px;
                border: solid white;
                border-width: 0 3px 3px 0;
                -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
            }
        }
        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
            &:checked {
                &~ .checkmark {
                    background-color: $red;
                    &:after {
                        display: block;
                    }
                }   
                &~ .wpcf7-list-item-label {
                    .checkmark {
                        background-color: $red;
                        &:after {
                            display: block;
                        }
                    }
                }
            }
        }
        .text-checkbox {
            margin-top: 5px;
            display: block;
            font-size: 1.2rem;
        }
    }
}

.form-privacy-check {
    margin-top: -40px;
    &.checkbox-privacy {
        .container-checkbox {
            padding-left: 0;
            .wpcf7-list-item {
                position: relative;
                .checkmark {
                    margin-top: 0;
                    left: -10px;
                }
            }
            .text-checkbox {
                padding-left: 30px;
                text-align: left;
            }
        }
    }
}

