.line-link {
    font-weight: 400;
    padding-bottom: 8px;
    border: none;
    outline: none;
    font-size: 2rem;
    line-height: 0.8;
    letter-spacing: .07em;
    border-radius: 0;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    display: inline-block;
    position: relative;
    overflow: hidden;
    background-color: transparent;
    font-family: $heading-font;
    &:after {
        content: "";
        height: 1px;
        width: 100%;
        position: absolute;
        bottom: 1px;
        left: 0;
        -ms-transform: translateX(0);
        transform: translateX(0);
        transition: transform .3s cubic-bezier(.215,.61,.355,1) .4s;
    }
    &:before {
        content: "";
        height: 1px;
        width: 100%;
        position: absolute;
        bottom: 1px;
        left: 0;
        -ms-transform: translateX(0);
        transform: translateX(0);
        transition: transform .3s cubic-bezier(.215,.61,.355,1) .4s;
        transition: transform .3s cubic-bezier(.215,.61,.355,1) 0ms;
        -ms-transform: translateX(-105%);
        transform: translateX(-105%);
    }
    &:hover {
        &:before {
            transition: transform .3s cubic-bezier(.215,.61,.355,1) .4s;
            -ms-transform: translateX(0);
            transform: translateX(0);
        }
        &:after {
            transition: transform .3s cubic-bezier(.215,.61,.355,1) 0ms;
            -ms-transform: translateX(105%);
            transform: translateX(105%);
        }
    }
    &.white {
        color: $white;
        &:after, &:before {
            background-color: $white;
        }
    }
    &.black {
        color: $black;
        &:after, &:before {
            background-color: $black;
        }
    }
}

