/******** SIDEBAR CART *********/

.site-cart {
    position: fixed;
    width: 100%;
    z-index: 1000;
    .site-cart__overlay {
        position: fixed;
        width: 100%;
        height: 100vh;
        background-color: #dedede;
        opacity: 0;
        z-index: 9999998;
        -webkit-transition: all Cubic-bezier(.33,1,.33,1) 0.7s;
        transition: all Cubic-bezier(.33,1,.33,1) 0.7s;
        visibility: hidden;
        pointer-events: none;
    }
    .site-cart__panel {
        position: fixed;
        background: $white;
        width: 35%;
        right: -35%;
        top: 0px;
        z-index: 9999999 !important;
        transition: all 0.3s cubic-bezier(0.33, 1, 0.33, 1) 0s;
        height: 100vh;
        overflow-y: scroll;
        padding-bottom: 40px;
        z-index: 200;
        border-left: 1px solid rgb(241, 239, 234);
        opacity: 0;
        .close-site-cart {
            position: absolute;
            padding: 3.333vw 2.778vw;
            right: 0;
            background-image: url(../../../../images/close.svg);
            height: 1.2rem;
            width: 1.2rem;
            background-size: 1.2rem;
            background-repeat: no-repeat;
            background-position: center center;
            text-indent: 99em;
            overflow: hidden;
            -webkit-box-sizing: content-box;
            box-sizing: content-box;
            top: -60px;
            z-index: 201;
            -webkit-transition: -webkit-transform .3s ease 0s;
            transition: -webkit-transform .3s ease 0s;
            transition: transform .3s ease 0s;
            transition: transform .3s ease 0s,-webkit-transform .3s ease 0s;
            transition: transform .3s ease 0s,-webkit-transform .3s ease 0s;
            -webkit-transform-origin: center center;
            transform-origin: center center;
            z-index: 9;
