.filter-attributes-product {
    position: absolute;
    z-index: 99;
    width: calc(100% - 30px);
    display: flex;
    align-items: center;
    background: $white;
    transition: ease-in-out all 0.5s;
    &.scroll {
        position: fixed;
        z-index: 9;
        width: calc(100%);
        transform: translateX(-15px);
        top: 0;
    }
    .clear_filters {
        padding: 7px;
        font-size: 1.2rem;
        background: $black;
        margin-left: auto;
        visibility: hidden;
        &.visibile {
            visibility: visible !important;
        }
        &.hidden {
            visibility: hidden !important;
        }
    }
    .filter_item {
        position: relative;
        background: $white;
        margin: 20px 0;
        &.filter-order {
            .filter-title {
                border-right: 0;
            }
            .filter-content {
                width: 200px;
                left: inherit;
                right: 0;
                .filter-content-checkbox {
                    margin-bottom: 0;
                }
                .filter-option {
                    label {
                        max-width: 100%;
                        flex: 0 0 100%;
                    }
                }
            }
        }
        .filter-title {
            padding: 0 35px 0 25px;
            position: relative;
            border-right: 2px solid #c3c3c3;
            font-weight: 300;
            text-transform: uppercase;
            font-size: 1.2rem;
            &:hover {
                cursor: pointer;
            }
            .arrow-icon {
                position: absolute;
                width: 12px;
                top: 43%;
                right: 12px;
                fill: #000;
                -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
                svg {
                    width: 10px;
                    transform: rotate(180deg);
                }
            }
            &.active {
                .arrow-icon {
                    svg {
                        transform: rotate(360deg);
                    }
                }
            }
        }
    }
    .filter-content {
        display: none;
        position: absolute;
        top: 42px;
        width: 300%;
        background: #fff;
        z-index: 9;
        left: -1px;
        padding: 20px;
        .filter-content-checkbox {
            margin-bottom: 20px;
        }
        .filter-option {
            display: flex;
            flex-wrap: wrap;
            margin: 0 -15px;
            label {
                max-width: 33.33%;
                flex: 0 0 33.33%;
                padding: 0 15px;
                cursor: pointer;
                .label-option__button {
                    text-transform: uppercase;
                    font-weight: 300;
                    transition: ease all 0.3s;
                    padding: 15px 5px;
                    font-size: 1.3rem;
                    .number-item {
                        color: #666;
                        font-style: italic;
                        font-size: 1.2rem;
                        transition: ease all 0.3s;
                    }
                    &:hover {
                        background: $black;
                        color: $white;
                        .number-item {
                            color: $white;
                        }
                    }
                }
                &.active {
                    .label-option__button {
                        background: $black;
                        color: $white;
                        .number-item {
                            color: $white;
                        }
                    }
                }
            }
            input {
                display: none;
            }
        }
        .apply_button {
            width: 100%;
            background: $black;
        }
    }
}


