.checkbox-privacy .container-checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  .checkbox-privacy .container-checkbox:hover input ~ .checkmark {
    background-color: #dadfe2; }
  .checkbox-privacy .container-checkbox .checkmark {
    position: absolute;
    top: 50%;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #dadfe2;
    font-size: 1.5rem;
    transform: translate(0, -50%);
    margin-top: 2px; }
    .checkbox-privacy .container-checkbox .checkmark:after {
      content: "";
      position: absolute;
      display: none;
      left: 9px;
      top: 5px;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg); }
  .checkbox-privacy .container-checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
    .checkbox-privacy .container-checkbox input:checked ~ .checkmark {
      background-color: #dc3545; }
      .checkbox-privacy .container-checkbox input:checked ~ .checkmark:after {
        display: block; }
    .checkbox-privacy .container-checkbox input:checked ~ .wpcf7-list-item-label .checkmark {
      background-color: #dc3545; }
      .checkbox-privacy .container-checkbox input:checked ~ .wpcf7-list-item-label .checkmark:after {
